<template>
  <div>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
      <!-- 领涨股 -->
      <template slot="name" slot-scope="name, record">
        <a @click="handleClickName(record)">{{record.name}}</a>
      </template>
      <!-- 当日资金流入 -->
      <template slot="capitalInflows" slot-scope="capitalInflows">
        <span :class="Number(capitalInflows/100).toFixed(2)>=0?'red':'green'">{{Number(capitalInflows/100).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 当日余额 -->
      <template slot="balance" slot-scope="balance">
        <span class="red">{{Number(balance/100).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 历史资金累计流入 -->
      <template slot="totalCapitalInflows" slot-scope="totalCapitalInflows">
        <span class="red">{{Number(totalCapitalInflows/100).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 买入成交额 -->
      <template slot="buyVolume" slot-scope="buyVolume">
        <span>{{Number(buyVolume/100).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 卖出成交额 -->
      <template slot="sellVolume" slot-scope="sellVolume">
        <span>{{Number(sellVolume/100).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 当日成交静买额 -->
      <template slot="netPurchase" slot-scope="netPurchase">
        <span :class="Number(netPurchase).toFixed(2)>=0?'red':'green'">{{Number(netPurchase).toFixed(2) + '亿元'}}</span>
      </template>
      <!-- 领涨股涨跌幅 -->
      <template slot="chgOfLedRising" slot-scope="chgOfLedRising">
        <span>{{Number(chgOfLedRising).toFixed(2) + '%'}}</span>
      </template>
      <!-- 涨跌幅 -->
      <template slot="chgOfIndex" slot-scope="chgOfIndex">
        <span :class="Number(chgOfIndex*100).toFixed(2)>=0?'red':'green'">{{Number(chgOfIndex*100).toFixed(2) + '%'}}</span>
      </template>
    </a-table>
  </div>
</template>

<script>
import {
  getSzHistoryData // 深股通历史数据
} from '@/api/index'
const columns = [
  { align: 'center', title: '日期', dataIndex: 'date' },
  { align: 'center', title: '当日资金流入', dataIndex: 'capitalInflows', scopedSlots: { customRender: 'capitalInflows' } },
  { align: 'center', title: '当日余额', dataIndex: 'balance', scopedSlots: { customRender: 'balance' } },
  { align: 'center', title: '历史资金累计流入', dataIndex: 'totalCapitalInflows', scopedSlots: { customRender: 'totalCapitalInflows' } },
  { align: 'center', title: '当日成交静买额', dataIndex: 'netPurchase', scopedSlots: { customRender: 'netPurchase' } },
  { align: 'center', title: '买入成交额', dataIndex: 'buyVolume', scopedSlots: { customRender: 'buyVolume' } },
  { align: 'center', title: '卖出成交额', dataIndex: 'sellVolume', scopedSlots: { customRender: 'sellVolume' } },
  { align: 'center', title: '领涨股', dataIndex: 'name', scopedSlots: { customRender: 'name' } },
  { align: 'center', title: '领涨股涨跌幅', dataIndex: 'chgOfLedRising', scopedSlots: { customRender: 'chgOfLedRising' } },
  { align: 'center', title: '上证指数', dataIndex: 'indexs' },
  { align: 'center', title: '涨跌幅', dataIndex: 'chgOfIndex', scopedSlots: { customRender: 'chgOfIndex' } }
]
export default {
  name: 'szHistroy',
  data() {
    return {
      tableData: [],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleClickName(record) {
      this.$router.push('/stockInfo?code=' + record.ledRisingId)
    },
    handleTableChange(pagination, filters, sorter) {
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.loading = true
      getSzHistoryData(data).then(res => {
        console.log('深股通历史数据')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    }
  }
}
</script>
